/**
 * 公共组件样式
 */
.e-icon_up {
  display: inline-block;
  position: relative;
  width: 14px;
  height: 8px;
}
.e-icon_up::before,
.e-icon_up::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  left: 0;
  border-bottom: 7px solid #FFFEFE;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
}
.e-icon_up::before {
  top: 0;
}
.e-icon_up::after {
  top: 1px;
  border-bottom-color: #3F3F43;
}
.e-icon_down {
  display: inline-block;
  position: relative;
  width: 14px;
  height: 8px;
}
.e-icon_down::before,
.e-icon_down::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  left: 0;
  border-top: 7px solid #FFFEFE;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
}
.e-icon_down::before {
  bottom: 0;
}
.e-icon_down::after {
  bottom: 1px;
  border-top-color: #3F3F43;
}
.e-icon_person {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  vertical-align: top;
}
.e-icon_video {
  display: inline-block;
  width: 84px;
  height: 84px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  vertical-align: top;
}

